@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
.CatView {
    width: 800px;
}
.AttributeItem {
    width: 100%;
    margin: 8px 5px;
    background: #ACACAC;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.AttributeItem:hover {
    cursor: pointer;
}

.AttributeItem--selected {
    background: #FFFFFF;
}

.AttributeItem__image {
    max-width: none;
    width: 100%;
}
.AttributeSelection {
    width: 100%;
    display: flex;
}
.ColorItem {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid #808183;
    margin: 2px;
    border-radius: 10px;
    width: 63px;
    height: 63px;
}

.ColorItem:hover {
    cursor: pointer;
}

.ColorItem__check {
    display: inline;
    z-index: 1;
}

.ColorItem__check--remove {
    display: none;
}
.ColorSection {
    display: flex;
    flex-direction: column;
    margin: 8px 0;
}

.ColorSection__title {
    font-size: 1.2rem;
    font-family: 'Poppins', sans-serif;
    margin: 10px 0;
    color: #808183;
}

.ColorSection__items {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.EditItem {
    width: 100px;
    height: 100px;
    border: 2px solid #808183;
    border-radius: 10px;
    margin: 2px;
    padding: 0;
    overflow: hidden;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.EditItem:hover {
    cursor: pointer;
}

.EditItem__check {
    display: inline;
    z-index: 1;
}

.EditItem__check--remove {
    display: none;
}

.EditItem__image {
    max-width: none;
    width: 100%;
    position: absolute;
}

.EditSection {
    display: flex;
    flex-direction: column;
}

.EditSection__title {
    font-size: 1.2rem;
    font-family: 'Poppins', sans-serif;
    margin: 10px 0;
    color: #808183;
}

.EditSection__items {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.EditOptions {
    display: flex;
    padding: 20px 30px;
    flex-direction: column;
    border-radius: 20px;
    background: #FFFFFF;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.25);
    margin: 10px 0;    
}

.EditOptions__title {
    font-family: 'Poppins', sans-serif;
    font-size: 1.5rem;
    color: #808183;
    text-align: center;
    margin: 5px 0;
}
.EditPanel {
    width: 100%;
    max-width: 670px;
}
.Creator {
    display: flex;
    width: 100%;
}
